<template>
  <div id="edit-employee">
      <h3>Edit Employee</h3>
      <div class="row">
        <form @submit.prevent="updateEmployee" class="col s12">
          <div class="row">
            <div class="input-field col s12">
              <label :class="{active: employee_id}">Employee ID#</label>
              <input disabled type="text" v-model="employee_id" required>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <label :class="{active: name}">Name</label>
              <input type="text" v-model="name" required>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <label :class="{active: dept}">Department</label>
              <input type="text" v-model="dept" required>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <label :class="{active: position }">Position</label>
              <input type="text" v-model="position" required>
            </div>
          </div>
          <input type="submit" class="btn">
          <router-link to="/" class="btn grey">Cancel</router-link>
        </form>
      </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-employee',
  data () {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null
      }
  },
  beforeRouteEnter: (to, from, next) => {
    db.collection('employees').where('id','==',to.params.employee_id).get().then
    (querySnapshot =>{
      querySnapshot.forEach(doc =>{
        next(vm => {
          vm.employee_id = doc.data().id,
          vm.name = doc.data().name,
          vm.dept = doc.data().dept,
          vm.position = doc.data().position  
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employees').where('id','==',this.$route.params.employee_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc =>{
          this.employee_id = doc.data().id,
          this.name = doc.data().name,
          this.dept = doc.data().dept,
          this.position = doc.data().position
        })
      })
    },
    updateEmployee () {
      db.collection('employees').where('id','==',this.$route.params.employee_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc =>{
          doc.ref.update({
            id: this.employee_id,
            name: this.name,
            dept: this.dept,
            position: this.position
          })
        })
      })
      .then (() => {
        this.$router.push({name: 'view-employee',params: {employee_id: this.employee_id}})
      })   
    }
  }
}
</script>




