<template>
  <div id="view-employee">
    <h4>view-employee</h4>
      <ul class="collection with-header">
        <li class="collection-header"><h4>{{name}}</h4></li>
        <li class="collection-item">Employee ID# : {{employee_id}}</li>
        <li class="collection-item">Department : {{dept}}</li>
        <li class="collection-item">position : {{position}}</li>
      </ul>
      <router-link to="/" class="btn grey">back</router-link>
      <button @click="deleteEmployee" class="btn red">delete</button>
      <div class="fixed-action-btn">
          <router-link :to="{name: 'edit-employee',params: {employee_id: employee_id}}" class="btn-floating btn-large red">
            <i class="fas fa-pencil-alt"></i>
          </router-link>
      </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-employee',
  data () {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter: (to, from, next) => {
    db.collection('employees').where('id','==',to.params.employee_id).get().then
    (querySnapshot =>{
      querySnapshot.forEach(doc =>{
        next(vm => {
          vm.employee_id = doc.data().id,
          vm.name = doc.data().name,
          vm.dept = doc.data().dept,
          vm.position = doc.data().position  
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employees').where('id','==',this.$route.params.employee_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc =>{
          this.employee_id = doc.data().id,
          this.name = doc.data().name,
          this.dept = doc.data().dept,
          this.position = doc.data().position
        })
      })
    },
    deleteEmployee () {
      if(confirm('are you sure?')){
        db.collection('employees').where('id','==',this.$route.params.employee_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc =>{
            doc.ref.delete()
            this.$router.push('/')
          })
        })
      }
    }
  }
}
</script>




